import { Select as MUISelect } from '@mui/material';
import { useShallowMemo } from '../../hooks/common';
import { MenuItem, Box, Typography } from '@mui/material';
import React, { useCallback } from 'react';
import { ComponentSchema } from '../../type';
const Select = (props) => {
  const { value, onChange, options, showOptionLabel, margin, padding } = props;

  const menuItems = useShallowMemo(
    () =>
      options.map((item) => (
        <MenuItem key={item.value} value={item.value}>
          <Box
            sx={{
              width: '100%',
              display: 'flex',
              flexWrap: 'nowrap',
              alignItems: 'center',
            }}
          >
            <Typography sx={{ font: 'inherit', flex: 1 }}>
              {item.label}
            </Typography>
            {showOptionLabel && (
              <Typography color="textDisabled" sx={{ fontSize: 11 }}>
                {item.value}
              </Typography>
            )}
          </Box>
        </MenuItem>
      )),
    [options, showOptionLabel],
  );

  const onValueChange = useCallback(
    (e) => onChange(e.target.value),
    [onChange],
  );

  return (
    <Box m={margin}>
      <MUISelect
        value={value}
        onChange={onValueChange}
        sx={{ padding }}
        fullWidth
      >
        {menuItems}
      </MUISelect>
    </Box>
  );
};

const schema: ComponentSchema = {
  type: 'select',
  name: '下拉框',
  layout: {
    width: 2,
    height: 10,
  },
  attributes: [
    {
      key: 'value',
      name: '值',
      widget: 'editor',
      defaultValue: '',
      accept: 'string',
      category: 'content',
    },
    {
      key: 'options',
      name: '选项',
      widget: 'list',
      defaultValue: [
        {
          value: 'option1',
          label: '选项一',
        },
        {
          value: 'option2',
          label: '选项二',
        },
        {
          value: 'option3',
          label: '选项三',
        },
      ],
      category: 'content',
      widgetProps: {
        mainField: 'value',
        fields: [
          {
            field: 'value',
            label: '值',
            widget: 'editor',
          },
          {
            field: 'label',
            label: '标签',
            widget: 'editor',
          },
        ],
      },
    },
  ],
  events: [
    {
      name: '值改变',
      type: 'onChange',
      bindState: 'value',
    },
  ],
};

Object.assign(Select, { schema });
export default Select;
