import { create } from 'zustand';
import evtManager from '../event';
import { EventType } from '../constant';
import { set as _set, cloneDeep } from 'lodash-es';

const useStatesStore = create<any>((set) => ({
  clearStates: () =>
    set((state) => {
      const newState = state;
      for (const key in newState) {
        if (typeof newState[key] === 'function') {
          continue;
        }
        delete newState[key];
      }
      return { ...newState };
    }),
  initialStates: (states) => set({ ...states }),
  setState: (id, newState, merge = true) =>
    set((state) => {
      const target = state[id];
      evtManager.emit(EventType.STATE_CHANGE, {
        id,
        newState: newState,
        merge,
      });
      if (merge && target) {
        return {
          [id]: { ...target, ...newState },
        };
      }
      return { [id]: newState };
    }),
  setStatePrecisely: (id, path, value) =>
    set((state) => {
      const target = cloneDeep(state[id]);
      _set(target, path, value);
      return {
        [id]: target,
      };
    }),
}));

Object.defineProperty(window, '__STATES__', {
  get() {
    return useStatesStore.getState();
  },
});

export { useStatesStore };
