const asyncQueue: any[] = [];
export const runAsync = (callback) => {
  asyncQueue.push(callback);
  window.postMessage({ type: 'runAsyncSignal' });
  const handler = (e) => {
    if (e.data.type !== 'runAsyncSignal') {
      return;
    }
    while (asyncQueue.length) {
      const cb = asyncQueue.shift();
      if (typeof cb === 'function') {
        cb();
      }
    }
    window.removeEventListener('message', handler);
  };
  window.addEventListener('message', handler);
};

export const makeReadOnly = (obj) => {
  return new Proxy(obj, {
    get(target, p, receiver) {
      const value = target[p];
      if (typeof value === 'object') {
        return makeReadOnly(value);
      }
      return value;
    },
    set(target, p, newValue, receiver) {
      console.error('object is readonly');
      return false;
    },
    ownKeys(target) {
      return Reflect.ownKeys(target);
    },
  });
};

export const onGithubIo = () => {
  return window.location.host.includes('github.io');
};

/**
 * 从 React 事件对象中获取值
 * @param event React 事件对象
 * @returns 事件对应的值
 */
export function getEventValue<T>(event: SyntheticEvent): T {
  if (typeof event === 'string') {
    return event;
  }
  const target = event.target as any;

  // 处理 checkbox 和 radio
  if (target.type === 'checkbox' || target.type === 'radio') {
    return (target as HTMLInputElement).checked as unknown as T;
  }

  // 处理文件输入
  if (target.type === 'file') {
    return (target as HTMLInputElement).files as unknown as T;
  }

  // 处理 select multiple
  if (target instanceof HTMLSelectElement && target.multiple) {
    return Array.from(target.selectedOptions).map(
      (option) => option.value,
    ) as unknown as T;
  }

  // 默认返回 value
  return target.value as unknown as T;
}

export const preview = () => {
  const previewWindow = window.open('/preview', '_blank');
  previewWindow!.onload = () => {
    runAsync(() => {
      previewWindow!.postMessage(
        {
          type: WindowMessageType.CANVAS_SCHEMA,
          data: useSchemaStore.getState().getSerializedSchema(),
        },
        'http://localhost:8080',
      );
    });
  };
  // 兼容 github.io spa
  if (onGithubIo()) {
    localStorage.setItem(
      'schemaData',
      JSON.stringify(useSchemaStore.getState().getSerializedSchema()),
    );
  }
};

function downloadFile(content, fileName, fileType) {
  const blob = new Blob([content], { type: fileType });

  const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = fileName || 'download';

  document.body.appendChild(a);
  a.click();

  setTimeout(() => {
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }, 100);
}

export const exportSchema = () => {
  const serilaizedSchema = useSchemaStore.getState().getSerializedSchema();
  downloadFile(
    JSON.stringify(serilaizedSchema, null, 2),
    `schema.${now()}.json`,
    'text/json',
  );
};

export const readFile = (cb, options) => {
  const { readType = 'text', fileType = '' } = options;
  const fileInput = document.createElement('input');
  fileInput.type = 'file';
  if (fileType) {
    fileInput.accept = fileType;
  }

  fileInput.addEventListener('change', (e) => {
    const file = fileInput.files?.[0];
    if (!file) {
      return;
    }
    const reader = new FileReader();
    if (!readType || readType === 'text') {
      reader.readAsText(file);
    }
    reader.onload = (e) => {
      const content = e.target?.result;
      cb(content);
    };
  });

  fileInput.click();
};

export const uploadSchema = () => {
  readFile(
    (content) => {
      try {
        const schema = JSON.parse(content);
        console.log('upload schema', schema);
        useSchemaStore.getState().initialSchema(schema);
        enqueueSnackbar('导入完成', {
          variant: 'success',
        });
      } catch (err) {
        console.error('schema upload error', err);
        enqueueSnackbar('导入失败', {
          variant: 'error',
        });
      }
    },
    { fileType: 'application/json' },
  );
};

export const isDev = () => {
  return (
    process.env.NODE_ENV === 'development' || process.env.NODE_ENV === 'dev'
  );
};

export const isProd = () => {
  return (
    process.env.NODE_ENV === 'production' || process.env.NODE_ENV == 'prod'
  );
};

export const isSystemInDarkMode = () => {
  return window.matchMedia('(prefers-color-scheme: dark)').matches;
};

export const devExpose = (key: string, value) => {
  if (isDev()) {
    Object.defineProperty(window, key, {
      get() {
        return typeof value === 'function' ? value() : value;
      },
    });
  }
};

export const safeParseJSON = (input: any, ...args: any[]) => {
  try {
    return JSON.parse(input, ...args);
  } catch (error) {
    console.error('json parse error', error);
    return {};
  }
};

export const safeStringifyJSON = (input: any, ...args: any[]) => {
  try {
    return JSON.stringify(input, ...args);
  } catch (error) {
    console.error('json stringify error', error);
    return '{}';
  }
};

const generateRevertOperation = (operation: OperationLog): OperationLog => {
  const { type, details } = operation;

  let revertOperation: Partial<OperationLog> = {
    operator: useGlobalStore.getState().userInfo.username,
    schema: useSchemaStore.getState().getSerializedSchema(),
    time: now(),
  };

  switch (type) {
    case OperationType.ADD_UI:
      revertOperation = {
        ...revertOperation,
        type: OperationType.REMOVE_UI,
        details: {
          revert: true,
          ...pick(details, 'id', 'itemType', 'name', 'type'),
        },
      };
      break;
    case OperationType.REMOVE_UI:
      revertOperation = {
        ...revertOperation,
        type: OperationType.ADD_UI,
        details: {
          revert: true,
          ...pick(details, 'id', 'itemType', 'name', 'type'),
        },
      };
      break;
    default:
      break;
  }

  return revertOperation as OperationLog;
};

export const undo = () => {
  const { operationStack, versionCursor } = useGlobalStore.getState();
  if (operationStack.length <= 1 || versionCursor <= 0) {
    return;
  }
  const operation = operationStack[versionCursor - 1];

  const { schema } = operation;
  const { initialSchema } = useSchemaStore.getState();
  initialSchema(safeParseJSON(schema));

  useGlobalStore.setState({ versionCursor: versionCursor - 1 });

  enqueueSnackbar('回滚（UNDO）', {
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'right',
    },
    variant: 'info',
    autoHideDuration: 3000,
    preventDuplicate: true,
  });

  // 异步新增revert操作日志
  // runAsync(() => {
  //   const revertOperation = generateRevertOperation(operation);
  //   recordOperation(revertOperation);
  // });
};

export const redo = () => {
  const { operationStack, versionCursor } = useGlobalStore.getState();
  if (versionCursor >= operationStack.length - 1) {
    return;
  }
  const operation = operationStack[versionCursor + 1];

  const { schema } = operation;
  const { initialSchema } = useSchemaStore.getState();
  initialSchema(safeParseJSON(schema));

  useGlobalStore.setState({ versionCursor: versionCursor + 1 });

  enqueueSnackbar('前进（REDO）', {
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'right',
    },
    variant: 'info',
    autoHideDuration: 3000,
    preventDuplicate: true,
  });
};

