import React from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { BrowserRouter, Routes, Route, useLocation } from 'react-router';
import Editor from './editor';
import Previewer from './previewer';
import { StrictMode } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Drawer,
  Box,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  ThemeProvider,
  CssBaseline,
  useTheme,
  Avatar,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import PlayIcon from '@mui/icons-material/PlayArrow';
import DownloadIcon from '@mui/icons-material/Download';
import UploadIcon from '@mui/icons-material/Upload';
import EditorIcon from '@mui/icons-material/Construction';
import { SnackbarProvider } from 'notistack';
import {
  devExpose,
  exportSchema,
  getOS,
  isDev,
  preview,
  redo,
  undo,
  uploadSchema,
} from './misc';
import Tooltip from './builtIn/components/Tooltip';
import { useState } from 'react';
import useGlobalStore from './globalStore';
import { themeConfig } from './theme';
import { useEffect } from 'react';
import { Undo } from '@mui/icons-material';
import { Redo } from '@mui/icons-material';
import { useMemo } from 'react';
import { useShallow } from 'zustand/react/shallow';
import { pick } from 'lodash-es';
import LazyEditor from './LazyEditor';
import Logo from './assets/images/ilowent.svg';

const TopMenu = ({ closeWrapper }) => {
  return (
    <Box sx={{ width: 250 }} role="presentation">
      <List disablePadding>
        <ListItem disablePadding>
          <ListItemButton
            onClick={() => {
              closeWrapper?.();
            }}
          >
            <ListItemIcon>
              <EditorIcon />
            </ListItemIcon>
            <ListItemText>编辑器</ListItemText>
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  );
};

const Platform = (props) => {
  const { children } = props;

  const location = useLocation();

  const [drawerOpen, setDrawerOpen] = useState(false);
  const toggleTheme = useGlobalStore((state) => state.toggleTheme);

  const theme = useGlobalStore((state) => state.theme);

  const { operationStack, versionCursor, userInfo } = useGlobalStore(
    useShallow((state) =>
      pick(state, 'operationStack', 'versionCursor', 'userInfo'),
    ),
  );
  const selectedTheme = themeConfig[theme];

  const muiTheme = useTheme();

  const os = useMemo(() => {
    return getOS();
  }, []);

  const [canUndo, canRedo] = useMemo(() => {
    return [versionCursor > 0, versionCursor < operationStack.length - 1];
  }, [operationStack, versionCursor]);

  const platformMode = useGlobalStore((state) => state.mode);

  return (
    <SnackbarProvider maxSnack={3} autoHideDuration={1000}>
      <CssBaseline />
      <Drawer
        open={drawerOpen}
        onClose={() => {
          setDrawerOpen(false);
        }}
        sx={{ zIndex: muiTheme.zIndex.drawer + 2 }}
      >
        <TopMenu closeWrapper={() => setDrawerOpen(false)} />
      </Drawer>
      <AppBar position="relative" sx={{ zIndex: muiTheme.zIndex.drawer + 1 }}>
        <Toolbar sx={{ minHeight: '30px !important', columnGap: 2 }}>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 4 }}
            onClick={() => {
              setDrawerOpen(true);
            }}
          >
            <MenuIcon />
          </IconButton>
          {/* <Typography variant="subtitle1" component="div" sx={{ flexGrow: 1 }} fontFamily={'serif'} fontWeight={1000} fontSize={20}>
            ILOWENT
          </Typography> */}
          <Box
            flex={1}
            sx={{
              '& path': {
                fill: 'var(--text-logo-0)',
              },
            }}
          >
            <Logo
              width={80}
              style={{
                transform: 'scaleX(1.5)',
              }}
            />
          </Box>
          {platformMode === 'edit' && (
            <Box flexGrow={1} display={'flex'} columnGap={1}>
              <Tooltip
                arrow
                hidden={os !== 'macOS' && os !== 'Windows'}
                title={`回滚（${os === 'macOS' ? '⌘' : '❖'}Z）`}
                placement="bottom"
              >
                <Box>
                  <IconButton
                    disabled={!canUndo}
                    color="inherit"
                    size="small"
                    onClick={() => undo()}
                  >
                    <Undo />
                  </IconButton>
                </Box>
              </Tooltip>
              <Tooltip
                arrow
                hidden={os !== 'macOS' && os !== 'Windows'}
                title={`前进（⇧${os === 'macOS' ? '⌘' : '❖'}Z）`}
                placement="bottom"
              >
                <Box>
                  <IconButton
                    disabled={!canRedo}
                    color="inherit"
                    size="small"
                    onClick={() => redo()}
                  >
                    <Redo />
                  </IconButton>
                </Box>
              </Tooltip>
            </Box>
          )}
          <Tooltip title="导入" placement="bottom" arrow offset={[0, -10]}>
            <IconButton color="inherit" size="small" onClick={uploadSchema}>
              <UploadIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="导出" placement="bottom" arrow offset={[0, -10]}>
            <IconButton color="inherit" size="small" onClick={exportSchema}>
              <DownloadIcon />
            </IconButton>
          </Tooltip>
          {location.pathname !== '/preview' && (
            <Tooltip title="预览" placement="bottom" arrow offset={[0, -10]}>
              <IconButton color="inherit" size="small" onClick={preview}>
                <PlayIcon />
              </IconButton>
            </Tooltip>
          )}
          <Tooltip title="切换主题" placement="bottom" arrow offset={[0, -10]}>
            <IconButton color="inherit" size="small" onClick={toggleTheme}>
              {selectedTheme.icon}
            </IconButton>
          </Tooltip>
          <Avatar
            src={userInfo.avatar}
            sx={{
              width: 28,
              height: 28,
              fontSize: 11,
              bgcolor: 'Highlight',
              fontWeight: 'bolder',
            }}
            children={userInfo.avatar ? null : userInfo.username}
          />
        </Toolbar>
      </AppBar>
      <div>{children}</div>
    </SnackbarProvider>
  );
};

const LowCode = () => {
  const theme = useGlobalStore((state) => state.theme);
  const selectedTheme = themeConfig[theme];
  useEffect(() => {
    document.documentElement.setAttribute('data-theme', theme);
    localStorage.setItem('theme', theme);
  }, [theme]);

  return (
    <ThemeProvider theme={selectedTheme.theme}>
      <StrictMode>
        <BrowserRouter>
          <Platform>
            <Routes>
              <Route index element={<LazyEditor />} />
              <Route path="preview" element={<Previewer />} />
            </Routes>
          </Platform>
        </BrowserRouter>
      </StrictMode>
    </ThemeProvider>
  );
};

export default () => {
  return (
    <DndProvider
      backend={HTML5Backend}
      options={{
        enableMouseEvents: true,
        scrollAngleRanges: undefined,
      }}
    >
      <LowCode />
    </DndProvider>
  );
};

setTimeout(() => {
  if (isDev()) {
    import('lodash-es').then((res) => {
      devExpose('_', res);
    });
  }
});
