import ReactCodeMirror, { EditorView } from '@uiw/react-codemirror';
import React from 'react';
import { ComponentSchema } from '../../type';
import { Box } from '@mui/material';
import { javascript } from '@codemirror/lang-javascript';
import { json } from '@codemirror/lang-json';
import { useState } from 'react';
import { useEffect } from 'react';
import useGlobalStore from '../../globalStore';

const langExts = {
  JSON: [json()],
  javascript: [javascript({ jsx: true })],
  typescript: [javascript({ jsx: true, typescript: true })],
  python: [() => import('@codemirror/lang-python').then((res) => res.python())],
  java: [() => import('@codemirror/lang-java').then((res) => res.java())],
  css: [() => import('@codemirror/lang-css').then((res) => res.css())],
  cpp: [() => import('@codemirror/lang-cpp').then((res) => res.cpp())],
  go: [() => import('@codemirror/lang-go').then((res) => res.go())],
  rust: [() => import('@codemirror/lang-rust').then((res) => res.rust())],
  markdown: [
    () => import('@codemirror/lang-markdown').then((res) => res.markdown()),
  ],
  html: [() => import('@codemirror/lang-html').then((res) => res.html())],
  xml: [() => import('@codemirror/lang-xml').then((res) => res.xml())],
  yaml: [() => import('@codemirror/lang-yaml').then((res) => res.yaml())],
  wast: [() => import('@codemirror/lang-wast').then((res) => res.wast())],
  sass: [() => import('@codemirror/lang-sass').then((res) => res.sass())],
  vue: [() => import('@codemirror/lang-vue').then((res) => res.vue())],
  sql: [() => import('@codemirror/lang-sql').then((res) => res.sql())],
};

const Code = (props) => {
  const { value, onChange, readOnly, language, autoWrapping, margin, padding } =
    props;
  const [ext, setExt] = useState([javascript()]);

  const theme = useGlobalStore((state) => state.theme);

  useEffect(() => {
    const extList: any[] = [];
    async function setExtensions() {
      const exts = langExts[language];
      for (const _ext of exts) {
        if (typeof _ext !== 'function') {
          extList.push(_ext);
          continue;
        }
        let result = _ext();
        result = result instanceof Promise ? await result : result;
        extList.push(result);
      }
      if (autoWrapping) {
        extList.push(EditorView.lineWrapping);
      }
      setExt(extList);
    }
    setExtensions();
  }, [language, autoWrapping]);

  return (
    <Box
      onMouseDownCapture={(e) => {
        e.stopPropagation();
      }}
      margin={margin}
    >
      <ReactCodeMirror
        height="100%"
        value={value}
        onChange={onChange}
        readOnly={readOnly}
        extensions={ext}
        theme={theme}
        style={{
          padding,
        }}
      />
    </Box>
  );
};

const schema: ComponentSchema = {
  type: 'code',
  name: '代码块',
  layout: {
    width: 3,
    height: 20,
  },
  attributes: [
    {
      key: 'value',
      name: '值',
      widget: 'editor',
      defaultValue: '',
      previewValue: `{}`,
      category: 'content',
      widgetProps: {
        tipProps: {
          titles: ['value', '字符串'],
          description: '展示的代码',
          example: '{{ input1.value }}',
        },
      },
    },
    {
      key: 'readOnly',
      name: '只读',
      widget: 'switch',
      defaultValue: false,
      category: 'interaction',
    },
    {
      key: 'language',
      name: '语言',
      widget: 'select',
      defaultValue: 'JSON',
      category: 'content',
      widgetProps: {
        showOptionLabel: false,
        options: [
          {
            value: 'JSON',
            label: 'JSON',
          },
          {
            value: 'javascript',
            label: 'javascript',
          },
          {
            value: 'typescript',
            label: 'typescript',
          },
          {
            value: 'html',
            label: 'html',
          },
          {
            value: 'python',
            label: 'python',
          },
          {
            value: 'java',
            label: 'java',
          },
          {
            value: 'go',
            label: 'go',
          },
          {
            value: 'cpp',
            label: 'c++',
          },
          {
            value: 'sql',
            label: 'sql',
          },
          {
            value: 'rust',
            label: 'rust',
          },
          {
            value: 'markdown',
            label: 'markdown',
          },
          {
            value: 'xml',
            label: 'xml',
          },
          {
            value: 'yaml',
            label: 'yaml',
          },
          {
            value: 'wast',
            label: 'wast',
          },
          {
            value: 'vue',
            label: 'vue',
          },
        ],
      },
    },
    {
      key: 'autoWrapping',
      name: '自动换行',
      widget: 'switch',
      defaultValue: false,
      category: 'interaction',
    },
  ],
  events: [
    {
      name: '输入',
      type: 'onChange',
      bindState: 'value',
    },
  ],
};

Object.assign(Code, { schema });

export default Code;
