import { TextField } from '@mui/material';
import React from 'react';
import { ComponentSchema, FormItemType } from '../../type';

const Input = (props) => {
  const {
    value,
    onChange,
    label,
    placeholder,
    color,
    helperText,
    onBlur,
    onFocus,
    margin,
    padding,
  } = props;

  return (
    <TextField
      value={value}
      onChange={onChange}
      label={label}
      placeholder={placeholder}
      color={color}
      fullWidth
      autoComplete="off"
      helperText={helperText}
      onFocus={onFocus}
      onBlur={onBlur}
      style={{
        margin,
        padding,
      }}
    />
  );
};

const schema: ComponentSchema = {
  type: 'input',
  name: '文字输入框',
  attributes: [
    {
      key: 'value',
      name: '值',
      widget: 'editor',
      defaultValue: '',
      category: 'content',
      widgetProps: {
        tipProps: {
          titles: ['value', '字符串'],
          description: '文本输入框的值',
          example: '{{ input1.label }}',
        },
      },
    },
    {
      key: 'label',
      name: '标签',
      widget: 'editor',
      category: 'content',
      defaultValue: '文本输入框',
      widgetProps: {
        tipProps: {
          titles: ['label', '字符串'],
        },
      },
    },
    {
      key: 'placeholder',
      name: '占位文字',
      widget: 'editor',
      category: 'content',
      defaultValue: '输入文本',
      widgetProps: {
        tipProps: {
          titles: ['placeholder', '字符串'],
        },
      },
    },
    {
      key: 'helperText',
      name: '帮助文字',
      widget: 'editor',
      defaultValue: '',
      category: 'content',
      widgetProps: {
        tipProps: {
          titles: ['helperText', '字符串'],
        },
      },
    },
    {
      key: 'color',
      name: '聚焦颜色',
      widget: 'select',
      defaultValue: 'primary',
      category: 'interaction',
      widgetProps: {
        tipProps: {
          titles: ['color', '字符串'],
        },
        options: [
          {
            value: 'primary',
            label: '主色',
          },
          {
            value: 'secondary',
            label: '副色',
          },
          {
            value: 'success',
            label: '成功',
          },
          {
            value: 'info',
            label: '提醒',
          },
          {
            value: 'warning',
            label: '警告',
          },
          {
            value: 'error',
            label: '错误',
          },
        ],
      },
    },
  ],
  events: [
    {
      type: 'onChange',
      name: '值改变',
      bindState: 'value',
    },
    {
      type: 'onFocus',
      name: '聚焦',
    },
    {
      type: 'onBlur',
      name: '失焦',
    },
  ],
  methods: [
    {
      type: 'setValue',
      name: '设置值',
      optionSchema: [
        {
          type: FormItemType.EDITOR,
          field: 'Input_Value',
          label: '值',
        },
      ],
      handler: (context) => {
        const { api, self, options } = context;
        api.setStates(self, { value: options.Input_Value });
      },
    },
  ],
  layout: {
    width: 2,
    height: 10,
    minHeight: 8,
  },
};

Object.assign(Input, { schema });

export default Input;
