import React from 'react';
import { Button as MUIButton } from '@mui/material';
import { ControlComponentContext, FormItemType } from '../../type';
import { Box } from '@mui/material';
import { useLayoutEffect } from 'react';
import { useRef } from 'react';

const Button = (props) => {
  const {
    text,
    click,
    variant,
    disabled,
    size,
    color,
    loading,
    margin,
    padding,
  } = props;

  return (
    <MUIButton
      onClick={click}
      variant={variant}
      disabled={disabled}
      size={size}
      color={color}
      loading={loading}
      style={{
        minWidth: 0,
        margin,
        flex: 1,
        padding,
      }}
    >
      {text}
    </MUIButton>
  );
};

Object.defineProperty(Button, 'schema', {
  get() {
    return {
      name: '按钮',
      type: 'button',
      attributes: [
        {
          key: 'text',
          widget: 'editor',
          name: '文本',
          accept: 'string',
          category: 'content',
          defaultValue: '按钮',
          widgetProps: {
            tipProps: {
              titles: ['text', '字符串'],
              description: '按钮展示的文本',
              example: '{{ button1.size }}',
            },
          },
        },
        {
          key: 'variant',
          widget: 'select',
          name: '类型',
          defaultValue: 'contained',
          category: 'appearance',
          widgetProps: {
            options: [
              {
                value: 'contained',
                label: '实心',
              },
              {
                value: 'outlined',
                label: '空心',
              },
              {
                value: 'text',
                label: '文字',
              },
            ],
          },
        },
        {
          key: 'size',
          widget: 'select',
          name: '尺寸',
          defaultValue: 'medium',
          category: 'appearance',
          widgetProps: {
            options: [
              {
                value: 'small',
                label: '小',
              },
              {
                value: 'medium',
                label: '中',
              },
              {
                value: 'large',
                label: '大',
              },
            ],
          },
        },
        {
          key: 'color',
          widget: 'select',
          name: '颜色',
          category: 'appearance',
          defaultValue: 'primary',
          widgetProps: {
            options: [
              {
                value: 'primary',
                label: '主色',
              },
              {
                value: 'secondary',
                label: '副色',
              },
              {
                value: 'success',
                label: '成功',
              },
              {
                value: 'info',
                label: '提醒',
              },
              {
                value: 'warning',
                label: '警告',
              },
              {
                value: 'error',
                label: '错误',
              },
            ],
          },
        },
        {
          key: 'disabled',
          widget: 'switch',
          category: 'interaction',
          name: '禁用',
          defaultValue: false,
        },
        {
          key: 'loading',
          widget: 'switch',
          category: 'interaction',
          name: '加载中',
          defaultValue: false,
        },
      ],
      events: [
        {
          type: 'click',
          name: '点击',
        },
      ],
      methods: [
        {
          type: 'setContent',
          name: '设置内容',
          optionSchema: [
            {
              type: FormItemType.EDITOR,
              field: 'content',
              label: '内容',
            },
          ],
          handler: (context: ControlComponentContext) => {
            const { self, api, options } = context;
            api.setStates(self, { text: options.content });
          },
        },
      ],
      layout: {
        width: 1,
        height: 5,
        minHeight: 4,
        aspectRatio: 2,
      },
    };
  },
});

export default Button;
