export class ErrorBoundary extends Component<any> {
  state = {
    hasError: false,
    error: null,
    errorInfo: null,
  };

  static getDerivedStateFromError(error) {
    return { hasError: true };
  }

  componentDidCatch(error: Error, errorInfo: ErrorInfo): void {
    console.error(error, errorInfo);
    this.setState({ error, errorInfo });
  }

  componentDidUpdate(
    prevProps: Readonly<any>,
    prevState: Readonly<{}>,
    snapshot?: any,
  ): void {
    if (prevProps.children !== this.props.children) {
      this.setState({ hasError: false, error: null, errorInfo: null });
    }
  }

  render() {
    if (this.state.hasError) {
      return (
        <Alert severity="error" icon={false}>
          渲染失败，具体原因请查看控制台
        </Alert>
      );
    }

    return this.props.children;
  }
}

export const withErrorBoundary = (Component) => {
  return (props) => {
    return (
      <ErrorBoundary>
        <Component {...props} />
      </ErrorBoundary>
    );
  };
};

export const withShadowDom = (Component) => {
  return function (props) {
    const { schemaNode = {} } = props;
    const hostRef = useRef<HTMLDivElement>(null);

    const [_Component, setComponent] = useState<any>(null);
    const theme = useGlobalStore((state) => state.theme);
    const selectedTheme = themeConfig[theme];
    useEffect(() => {
      if (!hostRef.current?.shadowRoot) {
        const shadowRoot = hostRef.current?.attachShadow({ mode: 'open' });
        const container = document.createElement('div');
        shadowRoot?.appendChild(container);

        const cache = createCache({
          key: 'css',
          prepend: true,
          container: container,
        });

        const cssString = find(
          window.__debugStyleElements__,
          (element) =>
            element.dataset['ctype']?.replace(/\-/g, '').toLowerCase() ===
            schemaNode.type?.toLowerCase(),
        )?.innerText;
        const Comp = (props) => {
          return (
            <>
              {createPortal(
                <CacheProvider value={cache}>
                  <style>{cssString}</style>
                  <Component {...props} />
                </CacheProvider>,
                container,
              )}
            </>
          );
        };

        setComponent(() => Comp);
      }

      return () => {};
    }, []);
    return (
      <div ref={hostRef}>{_Component ? <_Component {...props} /> : null}</div>
    );
  };
};

